import React from 'react';

const StoreHours = () => (
  <div className="store-hours">
    <h3>Store Hours</h3>
    <div className="store-hours-items">
      <div className="store-hours-item">
        <div className="item item-1">Monday</div>
        <div className="item item-2 green">8:00AM ~ 6:00PM</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Tuesday</div>
        <div className="item item-2 green">8:00AM ~ 6:00PM</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Wednesday</div>
        <div className="item item-2 green">8:00AM ~ 6:00PM</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Thursday</div>
        <div className="item item-2 green">8:00AM ~ 6:00PM</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Friday</div>
        <div className="item item-2 green">8:00AM ~ 6:00PM</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Saturday - Sunday</div>
        <div className="item item-2 darkRed">CLOSED</div>
      </div>
      <div className="store-hours-item">
        <div className="item item-1">Statutory Holidays</div>
        <div className="item item-2 darkRed">CLOSED</div>
      </div>
    </div>
  </div>
);

export default StoreHours;
