const DailySpecial = props => {
  const style = {
    background: `url('${props.item.image}')`,
    backgroundSize: 'cover',
    backgroundPosition: 'center center'
  };
  return (
    <div className="right-item">
      <div
        style={style}
        className={'right-item-inner'}
        onClick={() => {
          props.currentItem(props.item, props.index);
        }}
      >
        {/* <img
          src={props.item.image}
          alt={props.item.name}
          title={props.item.name}
        /> */}
        <div className="content">
          {props.item.day === '*Everyday' ? (
            <h5 className="everyday">{props.item.day}</h5>
          ) : (
            <h5>{props.item.day}</h5>
          )}
        </div>
      </div>
    </div>
  );
};

const DailySpecials = props => {
  const [curItem, setCurItem] = useState(DailySpecialData[0]);
  const leftItemRef = useRef(null);
  const rightItemRef = useRef(null);
  useEffect(() => {
    rightItemRef.current.children[0].children[0].classList.add('active');
  }, []);
  const setCurrentItem = (item, index) => {
    setCurItem(item);

    // leftItemRef.current.getElementsByTagName('img')[0].classList.add('animate');
    // setTimeout(() => {
    //   leftItemRef.current
    //     .getElementsByTagName('img')[0]
    //     .classList.remove('animate');
    // }, 1000);
    if (leftItemRef.current) leftItemRef.current.classList.add('animate');
    setTimeout(() => {
      if (leftItemRef.current) leftItemRef.current.classList.remove('animate');
    }, 1000);
    if (rightItemRef.current) {
      rightItemRef.current.querySelector('.active').classList.remove('active');
      rightItemRef.current.children[0].children[index].classList.add('active');
    }
  };
  const leftStyle = {
    background: `url('${curItem.image}') center center / 100% 100% no-repeat`
  };
  return (
    <>
      <h1 className="h1-heading-center mtb-10">Daily Special</h1>
      <div
        style={{
          background: 'url("/images/rose-hr-260.jpg")',
          backgroundSize: 'cover',
          width: '200px',
          height: '24px',
          margin: '0 auto'
        }}
      />
      <div className="seoulContainer daily-specials">
        <div
          style={{ ...leftStyle }}
          className="left"
          ref={leftItemRef}
          onTransitionEnd={e => {
            console.log('ended');
          }}
        >
          <p className="left-day">{curItem.day}</p>
          <p className="left-name">{curItem.name}</p>
          <p className="left-desc">{curItem.description}</p>
          {/* <img src={curItem.image} alt={curItem.name} /> */}
        </div>
        <div className="right" ref={rightItemRef}>
          <div className="right-inner">
            {DailySpecialData.map((item, i) => (
              <DailySpecial
                key={item.id}
                currentItem={setCurrentItem}
                item={item}
                index={i}
              >
                {item.day}
              </DailySpecial>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default DailySpecials;

