import React from 'react';
import PropTypes from 'prop-types';
// import '../styles/ToTop.scss';

const ToTop = props => (
  // <Top active={props.active} onClick={props.totoTop}>
  //   <i className="fas fa-chevron-up" />
  // </Top>
  <button
    className={'toTop ' + (props.show ? 'active' : '')}
    onClick={props.totoTop}
  >
    <i className="fas fa-chevron-up" />
  </button>
);

ToTop.propTypes = {
  show: PropTypes.bool
};

export default ToTop;
