import React from 'react';

// import '../styles/StoreInformation.scss';
import HorizontalLogo from './HorizontalLogo';

const StoreInfo = () => (
  <div className="store-info">
    <HorizontalLogo />
    <h3>Location</h3>
    <div className="store-info-items">
      <div className="store-info-item">
        <div className="item item-1">
          <a
            href="https://www.google.com/maps/place/Seoul+Food+Take-Out/@43.6719658,-79.3768762,15z/data=!4m5!3m4!1s0x0:0xe57c456e3a4c6ee4!8m2!3d43.6719658!4d-79.3768762"
            target="blank"
          >
            <i className="fas fa-map-marked-alt info-item" />
          </a>
        </div>
        <div className="item item-2">
          <p className="info-item address-tex">
            606 Sherbourne St, Toronto, ON M4X-1X6
          </p>
        </div>
      </div>
      <div className="store-info-item">
        <div className="item item-1">
          <a href="tel:4164500519">
            <i className="fas fa-phone info-item" />
          </a>
        </div>
        <div className="item item-2">
          <p className="info-item phone-text">(416) 450-0519</p>
        </div>
      </div>
    </div>
  </div>
);

export default StoreInfo;
