import React from 'react';
import GoogleMapReact from 'google-map-react';

// import '../styles/StoreInformation.scss';

class GoogleMap extends React.Component {
  static defaultProps = {
    center: {
      lat: 43.6719658,
      lng: -79.3768762
    },
    zoom: 15
  };
  // state = {
  //   height: ''
  // };
  // componentDidMount() {
  //   const height =
  //     window.innerWidth < window.innerHeight && window.innerWidth < 768
  //       ? '340px'
  //       : '440px';
  //   this.setState({ height });
  // }
  marker = (map, maps) => {
    return new maps.Marker({
      position: { lat: 43.6719658, lng: -79.3768762 },
      map,
      title: 'Seoul Food Take-Out'
    });
  };

  render() {
    // const { height } = this.state;
    return (
      // Important! Always set the container height explicitly
      <div
        // style={{
        //   height: height,
        //   width: '100%',
        //   background: 'rgba(0, 0, 0, 0.8)'
        // }}
        className="store-map"
      >
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyC9qr00vRspSrQUYXL8LCforFPZzcx3ifM' }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}
          options={this.getMapOptions}
          yesIWantToUseGoogleMapApiInternals
          onGoogleApiLoaded={({ map, maps }) => {
            this.marker(map, maps);
          }}
        >
          {/* <Marker lat={43.6719658} lng={-79.3768762} text="SFTO" /> */}
        </GoogleMapReact>
      </div>
    );
  }

  // getMapOptions = (maps: Maps) => {
  getMapOptions = maps => {
    return {
      streetViewControl: false,
      scaleControl: true,
      fullscreenControl: true,
      scrollwheel: false,
      styles: [
        {
          featureType: 'poi.business',
          elementType: 'labels',
          stylers: [
            {
              visibility: 'on'
            }
          ]
        }
      ],
      // gestureHandling: 'greedy',
      gestureHandling: 'cooperative',
      // gestureHandling: 'auto',
      disableDoubleClickZoom: true,
      minZoom: 13,
      maxZoom: 19,
      mapTypeControl: true,
      // mapTypeId: maps.MapTypeId.SATELLITE,
      mapTypeControlOptions: {
        style: maps.MapTypeControlStyle.HORIZONTAL_BAR,
        position: maps.ControlPosition.BOTTOM_CENTER,
        mapTypeIds: [
          maps.MapTypeId.ROADMAP,
          maps.MapTypeId.SATELLITE,
          maps.MapTypeId.HYBRID
        ]
      },

      zoomControl: true,
      clickableIcons: false
    };
  };
}

export default GoogleMap;
